@charset "UTF-8";
/* Global */
@font-face {
  font-family: "Opensans";
  src: local("Open Sans"), url("./fonts/opensans.woff2") format("woff2"), url("./fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Opensans";
  src: local("Open Sans"), local("Opensans-bold"), url("./fonts/opensansbold.woff2") format("woff2"), url("./fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: bold;
}
@font-face {
  font-family: "Opensans";
  src: local("Open Sans"), local("Opensans-light"), url("./fonts/opensanslight.woff2") format("woff2"), url("./fonts/opensanslight.woff") format("woff");
  font-weight: 300;
}
* {
  -webkit-tap-highlight-color: transparent;
}

/*Передаем property= параметр условия, Breakpoint= значение условия*/
.text {
  font-family: "Opensans", Arial, sans-serif;
  line-height: 1.5;
}
.text__small {
  font-weight: 400;
  font-size: 14px;
  color: #283645;
  line-height: 1.7em;
}
.text__small--spacing {
  line-height: 2.1em;
}
.text__small--red {
  color: #D22856;
}
.text__small--medium {
  font-size: 18px;
}
.text__normal {
  font-weight: 300;
  font-size: 24px;
  color: #FFFFFF;
  text-align: center;
  text-decoration: none;
  font-style: normal;
}
.text__normal--black {
  color: #283645;
}
.text__normal--red {
  color: #D22856;
}
.text__normal--medium {
  font-size: 18px;
}
.text__normal--small {
  font-size: 14px;
}
.text__normal--small12 {
  font-size: 12px;
}
.text__heading {
  font-weight: 700;
  font-size: 18px;
  color: #FFFFFF;
  text-align: center;
  font-style: normal;
  text-decoration: none;
}
.text__heading--black {
  color: #000000;
}
.text__heading--blue {
  color: #283645;
}
.text__heading--small {
  font-size: 16px;
}
.text__heading--small14 {
  font-size: 14px;
}
.text__heading--small12 {
  font-size: 12px;
}
.text__heading--big {
  font-size: 30px;
}

/* elements */
.burger {
  position: relative;
  border: none;
  cursor: pointer;
  background: transparent;
  min-width: 50px;
  display: flex;
  flex-flow: column;
  row-gap: 6px;
  align-items: center;
  padding: 0;
  user-select: none;
}
.burger:hover .burger__line {
  background-color: #D22856;
}
.burger:active .burger__line {
  opacity: 0.3;
}
.burger__line {
  width: 100%;
  height: 4px;
  background-color: #FFFFFF;
  box-sizing: border-box;
  border-radius: 1px;
  transition: 0.2s;
  user-select: none;
}

.backhead {
  background: url("./images/back-mobile.jpg") center center no-repeat;
  background-size: cover;
  padding-top: 66px;
}
@media only screen and (min-width: 660px) {
  .backhead {
    background-image: url("./images/back-tablet.jpg");
    padding-top: 184px;
  }
}
@media only screen and (min-width: 1200px) {
  .backhead {
    background-image: url("./images/back-desktop.jpg");
  }
}

.backgr:before, .backgr:after {
  background: #F2F2F2;
  content: "";
  display: block;
  height: 50%;
  left: 0;
  position: absolute;
  right: 0;
  z-index: -1;
  -webkit-backface-visibility: hidden;
}
.backgr:before {
  top: 0px;
  transform: skewY(-6deg);
  transform-origin: 100% 0;
}
.backgr:after {
  bottom: 0;
}
.backgr--deg3:before {
  transform: skewY(-3deg);
}

.input {
  width: 100%;
  box-sizing: border-box;
  border-radius: 1px;
  border: 2px solid #E5E5E5;
  padding: 19px 0px 19px 20px;
  line-height: 1;
}
.input:hover {
  border-color: #283645;
}
.input:focus {
  outline: 2px solid #D22856;
  outline-offset: -2px;
}
.input::placeholder {
  opacity: 1;
  color: #283645;
}
.input__label {
  display: block;
  margin-left: 21px;
}
@media only screen and (min-width: 660px) {
  .form__field--fio .input__label {
    margin-left: 0;
    margin-right: 18px;
    align-self: center;
  }
}

@media only screen and (min-width: 660px) {
  #userTel {
    background: url("./images/phone-icon.svg") right 19px center no-repeat, linear-gradient(#E5E5E5, #E5E5E5) right/59px 59px no-repeat;
  }
  #userEmail {
    background: url("./images/mail-icon.svg") right 20px center no-repeat, linear-gradient(#E5E5E5, #E5E5E5) right/59px 59px no-repeat;
  }
}
.checkbox {
  appearance: none;
  margin: 0;
  width: 35px;
  height: 35px;
  background: #FFFFFF;
  cursor: pointer;
  border-radius: 1px;
  border: 2px solid #E5E5E5;
}
.checkbox__label {
  align-self: center;
  padding-left: 21px;
  cursor: pointer;
}
.checkbox:checked {
  background: url("./images/checkbox-on.svg") center center no-repeat, #FFFFFF;
}
.checkbox:hover {
  border-color: #283645;
}
.checkbox:active {
  border-color: #D22856;
}

.radiobox {
  appearance: none;
  margin: 0;
  width: 37px;
  height: 37px;
  border-radius: 37px;
  border: 2px solid #E5E5E5;
  background-color: #FFFFFF;
  cursor: pointer;
}
.radiobox:checked {
  background: url("../images/radiobox-on.svg") center center no-repeat, #FFFFFF;
}
.radiobox:hover {
  border-color: #283645;
}
.radiobox:active {
  border-color: #D22856;
}
.radiobox__label {
  padding-left: 31px;
  align-self: center;
  cursor: pointer;
}
@media only screen and (min-width: 660px) {
  .radiobox__label {
    padding-left: 20px;
  }
}

.textarea {
  resize: none;
  height: 197px;
  width: 100%;
  border: 2px solid #E5E5E5;
  box-sizing: border-box;
  padding: 20px;
}

.fieldset-back {
  background-color: #F2F2F2;
  width: 100%;
}
@media only screen and (min-width: 660px) {
  .fieldset-back {
    display: contents;
  }
}

/* blocks */
.container {
  padding: 0 20px;
}
@media only screen and (min-width: 660px) {
  .container {
    padding: 0 3%;
  }
}
@media only screen and (min-width: 1200px) {
  .container {
    padding: 0 130px;
  }
}

.page {
  height: 100%;
}
.page__body {
  min-height: 100%;
  max-width: 1200px;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  overflow: hidden scroll;
  margin: 0 auto;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.2);
  display: grid;
  grid-template-rows: 1fr min-content;
}
@media (prefers-color-scheme: dark) {
  .page {
    background: #283645;
  }
  .page__body {
    background: #FFFFFF;
  }
}

.visually-hidden {
  color: transparent;
  font-size: 0;
  height: 0;
  position: absolute;
}

@keyframes ani {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.main-nav {
  width: 100%;
  display: none;
  opacity: 0;
  animation: ani 0.5s forwards;
}
.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.main-nav__item {
  height: 63px;
  border-bottom: solid 1px #000000;
  border-top: solid 1px rgba(255, 255, 255, 0.15);
}
.main-nav__link {
  box-sizing: border-box;
  display: block;
  text-decoration: none;
  color: inherit;
  width: 100%;
  padding: 18px 0;
  text-transform: uppercase;
  transition: 0.1s;
}
.main-nav__link:hover {
  color: #D22856;
}
.main-nav__link:active {
  opacity: 0.3;
}
@media only screen and (min-width: 1200px) {
  .main-nav {
    display: flex;
    z-index: 3;
    min-height: 100%;
    flex-wrap: wrap;
  }
  .main-nav__burger {
    display: none;
  }
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    margin: auto;
    margin-right: 0;
    column-gap: 78px;
  }
  .main-nav__link {
    font-weight: 400;
    line-height: 1;
    text-transform: none;
    padding-top: 25px;
    padding-bottom: 18px;
  }
  .main-nav__item {
    border: none;
  }
  .main-nav__item--active {
    border-bottom: white 2px solid;
  }
}

.slider {
  position: relative;
  touch-action: pan-y;
}
.slider__item {
  width: 100%;
  transition: 0.5s ease-in;
  opacity: 0;
  position: absolute;
  z-index: -1;
  display: block;
  left: 0;
  top: 0;
  transform: translateX(-50%);
}
.slider__item--active {
  opacity: 1;
  position: static;
  transform: translateX(0);
  transition: 0.3s ease-in;
  z-index: 1;
}
.slider__toggles {
  display: flex;
  justify-content: center;
  column-gap: 15px;
  align-items: center;
  height: 12px;
}
.slider__toggle {
  padding: 0;
  margin: 0;
  border: none;
  position: relative;
  background: none;
  cursor: pointer;
  width: 15px;
  height: 15px;
}
.slider__toggle::before {
  content: "";
  display: block;
  width: 6px;
  height: 6px;
  background-color: #283645;
  opacity: 0.3;
  border-radius: 6px;
  transition: 0.1s;
  scale: 0.9;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.slider__toggle:hover {
  opacity: 0.6;
}
.slider__toggle:active {
  opacity: 0.1;
}
.slider__toggle--current::before {
  box-sizing: content-box;
  background-color: #FFFFFF;
  border: 3px solid #283645;
  opacity: 1;
}
.slider__toggle--current::before:hover {
  opacity: 1;
}
.slider__toggle--current::before:active {
  transition: 0.2s;
  scale: 0.75;
}
.slider__btn {
  width: 60px;
  height: 60px;
  z-index: 5;
  border: none;
  cursor: pointer;
  background: url("./images/icon-arrow.svg") center right no-repeat;
  margin: 0;
  padding: 0;
  opacity: 0.3;
  transition: all 0.2s ease-in-out;
}
.slider__btn--right {
  rotate: 180deg;
}
.slider__btn--right:hover {
  transform: translate(-50%, 50%) scale(1.5);
}
.slider__btn--left:hover {
  transform: translate(-50%, -50%) scale(1.5);
}
.slider__btn:hover {
  opacity: 0.6;
}
.slider__btn:active {
  opacity: 0.1;
}

.logo {
  display: block;
  cursor: pointer;
}
.logo__img {
  display: block;
}

