  padding-top: 40px;
  box-shadow: 0px 7px 15px 0px rgba(0, 1, 1, 0.15);
  border-radius: 1px;
  max-width: 460px;
  box-sizing: border-box;
  position: fixed;
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  animation: popup 0.4s;
  animation-timing-function: ease-in-out;
}
.modal__title {
  margin: 0;
}
.modal__text {
  text-align: center;
  margin-top: 30px;
  margin-bottom: 37px;
}
.modal__button {
  width: 100%;
  max-width: 300px;
  display: block;
  margin: 0 auto;
}
.modal__wrap-button {
  background-color: #E5E5E5;
  padding: 52px 20px;
}
