  }
  .editor__range {
    width: 288px;
    max-width: initial;
  }
  .editor__turn {
    margin-right: 40px;
  }
  .editor__turn .crop {
    width: initial;
    height: initial;
  }
  .editor__turn .fill {
    width: initial;
    height: initial;
  }
  .editor__turn .contrast {
