/*sections from form-page*/
.form {
  padding-top: 50px;
  padding-bottom: 20px;
}
.form__title {
  text-align: center;
  margin-top: 0;
  margin-bottom: 10px;
}
.form__span {
  display: none;
}
.form__set {
  display: flex;
  flex-wrap: wrap;
}
.form__field {
  border: none;
  padding-top: 40px;
  padding-bottom: 42px;
  margin: 0;
  width: 100%;
  box-sizing: border-box;
}
.form__legend {
  padding: 40px 30px 0;
  margin: 0 auto;
  text-align: center;
  text-transform: uppercase;
  line-height: 1.3em;
}
.form__legend--hidden {
  position: absolute;
  opacity: 0;
  z-index: -1;
}
.form__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  row-gap: 26px;
}
.form__list--checkbox {
  row-gap: 43px;
}
.form__list--radiobox {
  row-gap: 34px;
}
.form__item {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  row-gap: 15px;
}
.form__button {
  width: 100%;
  max-width: 276px;
  margin: 0 auto;
}
.form__note {
  width: 100%;
  text-align: center;
  text-transform: uppercase;
  margin-top: 40px;
  margin-bottom: 0;
}
.form__note span {
  color: #D22856;
}
@media only screen and (min-width: 660px) {
