  }
  .form__field--contacts .form__item {
    width: 46%;
  }
  .form__field--textarea {
    padding-top: 47px;
    padding-bottom: 44px;
  }
  .form__list--radiobox {
    margin-left: 72px;
    row-gap: 39px;
  }
  .form__list--checkbox {
    justify-content: space-between;
